<template>
  <div>
    <div class="vertically-center vh100">
      <div class="wrapper margin--center">
          <h1 class="font-size--9 font-weight--700">Hey, I'm Gary.</h1>
          <h2 class="font-size--7 font--gray--500 font-weight--400">I am a developer and designer living in Cleveland, Ohio. I make sites and applications on the internet.</h2>
          <div class="margin--center">
            <a href="#portfolio" class="button button--lg">See What I've Built</a>
          </div>
      </div>
    </div>
    <!-- portfolio -->
    <div class="portfolio background--gray--200 vertically-center" id="portfolio">
      <div class="margin--center wrapper">
        <h2 class="font-size--8 padding-y--1 font-weight--700 text--center">I've built a few cool projects.</h2>
        <div class="grid grid--gutters">

          <div v-for="project in projects" class="grid--item">
            <a :href="project.url" target="_blank">
              <div class="card text--center">
                <div class="circle grid">
                  <span class="font-size--6">{{project.icon}}</span>
                </div>
                <h3 class="font-size--7 margin--b-0">
                  {{project.name}}
                </h3>
                <p class="font-size--2">{{project.language}}</p>
                <p class="font-size--5">{{project.description}}</p>
                <span class="button">View project</span>
              </div>
            </a>
          </div>

        </div>
      </div>
    </div>

    <!--div class="padding-y--8">
      <div class="wrapper--lg margin--center">
        <h2 class="font-size--8 margin--b-4 text--center">And I've written code for nice companies.</h2>

        <div class="grid margin--center grid--gutters grid--wrap-mobile">
          <div class="grid--item grid grid--center">
            <img src="../assets/client-logos/cvs.svg" alt="cvs">
          </div>
          <div class="grid--item grid grid--center">
            <img src="../assets/client-logos/papa-johns.svg" alt="papa-johns">
          </div>
          <div class="grid--item grid grid--center">
            <img src="../assets/client-logos/american-red-cross.svg" alt="american-red-cross">
          </div>
          <div class="grid--item grid grid--center">
            <img src="../assets/client-logos/gnc.svg" alt="gnc">
          </div>
          <div class="grid--item grid grid--center">
            <img src="../assets/client-logos/keybank.svg" alt="keybank">
          </div>
          <div class="grid--item grid grid--center">
            <img src="../assets/client-logos/indiana-university.svg" alt="indiana-university">
          </div>
        </div>
      </div>
    </div-->

  </div>
</template>
<script>
import helpers from './../helpers'

export default {
  name: 'Home',
  data () {
    return {
      projects: ''
    }
  },
  methods: {
    getProjects: function () {
      return this.$http.get('https://api.github.com/users/garybunofsky/repos').then(response => {
        var projectsIds = [77171025, 107911630, 37658188, 53368074]
        this.projects = response.body.filter(function (project) {
          return projectsIds.includes(project.id)
        })
        console.log("Hi, it's Gary.")
        console.log("Here are some of the things I've made:")
        this.projects.map(function (project) {
          project.name = helpers.capitalize(project.name)
          project.icon = project.description.slice(0, 2)
          project.description = project.description.substr(2)
          if (project.homepage == null || project.id !== 53368074) {
            project.url = project.homepage
            console.log("- " + project.name + ": " + project.homepage)
          } else {
            project.url = project.html_url
            console.log("- " + project.name + ": " + project.html_url)
          }
        })
      }, response => {})
    }
  },
  beforeMount () {
    this.getProjects()
  },
  filters: {
    truncate (string) {
      return string.substring(0, 108) + '...'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass?outputStyle=expanded" scoped>
// Colors
$blue--700: #0C71F3;
$blue--600: #318bff;
$blue--500: #5ba3ff;
$blue--400: #7fb7ff;
$blue--300: #eef5fd;
$blue--200: #f3f8ff;

$green--700: #37cfdb;
$green--600: #4ce0eb;
$green--500: #7dedf6;
$green--400: #9ef1f8;
$green--300: #b7f3f8;
$green--200: #cafbff;

$gray--700: #2a3338;
$gray--600: #434a53;
$gray--500: #63686e;
$gray--400: #d1d5d9;
$gray--300: #e5e7e9;
$gray--200: #fafafa;

.portfolio .grid {
  flex-direction: row;
}

.profile {
  border-radius:50%;
  height:6em;
  width:6em;
}
.circle {
  align-items: center;
  margin: 0 auto 1em auto;
  background:$blue--200;
  border-radius: 50%;
  display: flex;
  height: 65px;
  justify-content: center;
  width: 65px
}
.circle span {
  margin-bottom: 0;
}
.card {
  padding:4em 1.5em 2em 1.5em;
}
.portfolio {
  padding: 4em 0;
}
.portfolio .grid {
  flex-direction: column;
}
.portfolio .grid .grid--item:last-child {
  margin-bottom: 4em;
}
.companies img {
  max-width: 120px;
  height: auto;
}
@media screen and (min-width:960px) {
  .portfolio {
    padding: 0;
  }
}
</style>



